import React from 'react';
import {
  FormControl,
  FormLabel,
  FormErrorMessage,
  Input,
  Button,
  Flex,
  usePrefersReducedMotion,
  Text,
} from '@chakra-ui/react';
import { useForm } from 'react-hook-form';
import { useNavigate } from 'react-router-dom';
import users from '../../../mocks/users';

function Formulario() {
  let navigate = useNavigate();

  const {
    handleSubmit,
    register,
    formState: { errors, isSubmitting },
  } = useForm();

  const verificaUsuario = ({ agencia, conta, senha }) => {
    return users.find(
      user =>
        user.agencia === agencia && user.conta === conta && user.senha === senha
    );
  };

  function onSubmit(values) {
    return new Promise(resolve => {
      setTimeout(() => {
        alert(JSON.stringify(values, null, 2));
        resolve();

        // Verifica se existe usurio cadastrado
        if (verificaUsuario({ ...values })) {
          // Navegao para o Incio
          navigate('/inicio');
        }
      }, 3000);
    });
  }

  return (
    <Flex direction={'column'}>
      <form onSubmit={handleSubmit(onSubmit)}>
        <FormControl isInvalid={errors.agencia} w={'25vw'}>
          <FormLabel htmlFor="agencia">Agncia</FormLabel>
          <Input
            id="agencia"
            placeholder="Agncia"
            type="number"
            size="sm"
            {...register('agencia', {
              required: 'Campo obrigatrio',
              maxLength: {
                value: 4,
                message: 'O nmero da agncia no pode exceder 4 dgitos',
              },
            })}
          />

          <FormErrorMessage>
            {errors.agencia && errors.agencia?.message}
          </FormErrorMessage>
        </FormControl>

        <FormControl isInvalid={errors.conta} w={'25vw'}>
          <FormLabel htmlFor="conta">Conta</FormLabel>
          <Input
            id="conta"
            placeholder="Conta"
            type="number"
            size="sm"
            {...register('conta', {
              required: 'Campo obrigatrio',
              maxLength: {
                value: 8,
                message: 'O nmero da conta no pode exceder 8 dgitos',
              },
            })}
          />
          <FormErrorMessage>
            {errors.conta && errors.conta?.message}
          </FormErrorMessage>
        </FormControl>

        <FormControl isInvalid={errors.senha} w={'25vw'}>
          <FormLabel htmlFor="senha">Senha</FormLabel>
          <Input
            id="senha"
            placeholder="Senha"
            type={'password'}
            size="sm"
            {...register('senha', {
              required: 'Campo obrigatrio',
              minLength: {
                value: 8,
                message: 'A senha deve ter no mnimo 8 dgitos',
              },
            })}
          />
          <FormErrorMessage>
            {errors.senha && errors.senha?.message}
          </FormErrorMessage>
        </FormControl>

        <Button
          w="100%"
          mt={4}
          colorScheme="teal"
          isLoading={isSubmitting}
          type="submit"
        >
          Entrar
        </Button>
      </form>
      
      <Text>J possui conta?</Text>

      <Button
        mt={4}
        colorScheme="teal"
        type="button"
        onClick={() => navigate('/cadastro')}
      >
        Criar conta
      </Button>
    </Flex>
  );
}

export default Formulario;
